.faq {
    background: linear-gradient(135deg, rgba(75, 0, 130, 0.1), rgba(0, 0, 102, 0.1)); /* Opacity set in RGBA */

    padding: 19rem 10rem 0 9rem;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;

    height: 100vh;
}

@media only screen and (max-width: 768px) {
    .faq {
        padding: 8rem 2rem;
        display: flex;
        flex-direction: column;
        width: 100%;
        height: auto;
    }

}

.faq-container-left {
    color: #000;
    font-weight: 700;
    width: 30%;

}

@media only screen and (max-width: 768px) {
    .faq-container-left {
        width: 100%;
        margin-bottom: 5rem
    }

}

.faq-container-right {
    width: 50%;
}

@media only screen and (max-width: 768px) {
    .faq-container-right {
        width: 100%;

    }

}

.faq-container-left h3 {
    font-size: 2rem;
    margin-bottom: 1rem;
    
}
.faq-container-left h4 {
    font-size: 4.5rem;
    margin-bottom: 1.4rem;
}

.faq-container-left p {
    font-size: 1.6rem;

}

.faq-item {
    margin-bottom: 4rem;
    font-size: 1.8rem;
}

.faq-item h4 {
    font-size: rem;
    margin-bottom: 1rem;
    font-size: 2.5rem;
}